#skills {
    background-color: #f5f5f5;
    padding: 50px 0;
  }
  
  .skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    max-width: 1000px;
    margin: 0 auto;
  }
  
  .skills-card {
    width: 300px;
    margin: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    text-align: center;
    padding: 20px;
  }
  
  .skills-card-img {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    display: block;
  }
  
  .skills-card-name {
    font-size: 24px;
    margin-top: 10px;
    margin-bottom: 0;
  }
  