/* Navbar styles */
#nav-menu {
  background-color: #333;
  color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
  font-weight: bold;
}

#nav-menu ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

#nav-menu li {
  margin: 0 10px;
}

#nav-menu a {
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease;
  font-size: 16px;
  font-weight: 600;
}

#nav-menu a:hover {
  color: #ff8c00;
}

/* Active class for current page */
.active {
  color: #ff8c00 !important;
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  #nav-menu {
    flex-direction: column;
    align-items: center;
    padding: 20px 0;
  }

  .nav-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .nav-logo {
    font-size: 24px;
    font-weight: 700;
    color: #fff;
    text-decoration: none;
    transition: all 0.3s ease;
  }

  .nav-logo:hover {
    color: #ff8c00;
  }

  .nav-toggle {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    color: #fff;
    cursor: pointer;
  }

  #nav-menu ul {
    margin-top: 20px;
    flex-direction: column;
    align-items: center;
    display: none;
  }

  #nav-menu ul.open {
    display: flex;
  }

  #nav-menu li {
    margin: 10px 0;
  }

  #nav-menu a {
    font-size: 18px;
  }
}

/* Tablet Styles */
@media screen and (min-width: 769px) and (max-width: 1024px) {
  #nav-menu ul {
    margin-left: auto;
    margin-right: 30px;
  }
}

/* Desktop Styles */
@media screen and (min-width: 1025px) {
  #nav-menu ul {
    margin-left: auto;
    margin-right: 50px;
  }
}
