#contact {
  background-color: #f2f2f2;
  padding: 50px 0;
}

.contact h2 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
}

.contact ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contact li {
  margin-bottom: 20px;
}

.contact .icon {
  margin-right: 10px;
  font-size: 24px;
  vertical-align: middle;
}

.contact a {
  font-size: 18px;
  color: #777;
}

.contact a:hover {
  color: #222;
}

#contact-phone {
  font-size: 18px;
  color: #777;
}
