/* Home section styles */
#home {
    background-color: #f8f8f8;
    padding: 100px 0;
  }
  
  .home-img {
    text-align: center;
  }
  
  .home-img img {
    border-radius: 50%;
    /* height: 300px; */
    /* width: 300px;  */
  }
  
  .home-text {
    padding: 0 50px;
  }
  
  #user-detail-name {
    color: #333;
    font-size: 40px;
    font-weight: 700;
    margin-top: 50px;
  }
  
  #user-detail-intro {
    color: #666;
    font-size: 18px;
    font-weight: 500;
    margin-top: 30px;
    margin-bottom: 50px;
  }
  
  .social-links a {
    color: #333;
    font-size: 24px;
    margin-right: 20px;
    transition: all 0.3s ease;
  }
  
  .social-links a:hover {
    color: #ff8c00;
  }
  
  #resume-button-2 {
    background-color: #ff8c00;
    border: none;
    border-radius: 30px;
    color: #fff;
    cursor: pointer;
    font-size: 18px;
    font-weight: 600;
    padding: 15px 30px;
    text-align: center;
    text-decoration: none;
    transition: all 0.3s ease;
  }
  
  #resume-button-2:hover {
    background-color: #333;
    color: #fff;
  }
  
  @media (max-width: 768px) {
    .home-img img {
      height: 200px;
      width: 200px;
    }
  
    .home-text {
      padding: 0;
    }
  
    #user-detail-name {
      font-size: 32px;
      margin-top: 30px;
    }
  
    #user-detail-intro {
      font-size: 16px;
      margin-top: 20px;
      margin-bottom: 30px;
    }
  }
  